<template>
  <v-container
    id="Profile"
    fluid
    tag="section"
  >
    <div class="grid grid-cols-1 md:grid-cols-3 gap-3">
      <div class="col-span-2">
        <material-card
          color="primary"
          full-header
        >
          <template #heading>
            <div class="flex text-2xl font-semibold gap-2 p-4 place-items-center">
              <div class="p-5 rounded-md text-white bg-sky-600 hover:bg-sky-800" @click="edited" title="enable">
                <i class="fa-solid fa-pen"></i>
              </div>
              <div class="text-white">
                Edit User
              </div>
            </div>
          </template>
          <v-card-text>
            <v-row>
              <v-col
                cols="12"
                md="12"
              >
              <v-text-field
                v-model="name"
                label="Fullname"
                :disabled="dis"
                filled
              />
              <v-text-field
                v-model="phone"
                type="number"
                label="Phone number"
                :disabled="dis"
                filled
              />
              <v-text-field
                v-model="email"
                type="email"
                label="Email"
                :disabled="dis"
                filled
              />
              <v-text-field
                v-model="address"
                label="Address"
                :disabled="dis"
                filled
              />
              <v-text-field
                v-model="zipcode"
                label="Zip code"
                :disabled="dis"
                filled
              />
              <v-textarea
                v-model="des"
                label="Descirption..."
                :disabled="dis"
                filled
              />
              <v-text-field
                v-model="password"
                label="Password"
                type="password"
                :disabled="dis"
                filled
              />
              <div class="p-3 flex justify-end place-items-center gap-2">
                <v-btn
                  color="info"
                  max-width="150"
                  :disabled="dis"
                  @click="sv"
                >
                  <i class="fa-solid fa-floppy-disk mx-1"></i> save
                </v-btn>
                <v-btn
                  color="success"
                  max-width="150"
                  :disabled="dis"
                  @click="upd"
                >
                  <i class="fa-solid fa-seedling mx-1"></i> update
                </v-btn>
              </div>
              </v-col>
            </v-row>
          </v-card-text>
        </material-card>
      </div>
      <div>
        <template>
          <v-card
            class="mx-auto"
            max-width="400"
          >
            <v-img
              class="white--text align-end"
              height="200px"
              :src="url"
              @mousedown.right="minp"
            >
              <v-card-title
                v-text="username"
                class="text-h4 font-weight-medium capitalize"
              />
            </v-img>

            <v-card-subtitle
              v-text="address"
            />

            <v-card-text
              class="text--primary"
              v-text="des"
            />

            <v-card-actions>
              <v-btn
                color="blue"
                icon
                :title="fb"
              >
              <i class="fa-brands fa-facebook text-xl"></i>
              </v-btn>
              <v-btn
                color="blue"
                icon
                :title="twitter"
              >
              <i class="fa-brands fa-twitter text-xl"></i>
              </v-btn>
              <v-btn
                color="error"
                icon
                :title="email"
              >
              <i class="fa-solid fa-envelope text-xl"></i>
              </v-btn>
            </v-card-actions>
          </v-card>
        </template>
      </div>
    </div>
    <div class="mb-16"></div>
  </v-container>
</template>
<script>
export default {
  data: () => {
    return {
      username: 'anonim',
      address: 'New Hampshire, Manchester, 3267 Elliott Street',
      email: 'hola.pes@me.io',
      fb: 'fb.me/hola',
      twitter: 'tw.me/hola',
      des: 'I wanna ...',
      dis: true,
      url: 'http://localhost:8080/img/R.jpg',
    }
  },
  methods: {
   edited(){
    if (this.dis == true) {
      this.dis = false
    }else{
      this.dis = true
    }
   },
   sv(){
    var uname = JSON.parse(sessionStorage.getItem("admin"))
    if ((null && undefined) == this.password ) {
      var profile = {
        name: this.name,
        username: uname[0].username,
        email: this.email,
        password: uname[0].password,
        description: this.des,
        phone: this.phone,
        address: this.address,
        zipcode: this.zipcode
        }
      var g = localStorage.setItem("profile", JSON.stringify(profile))
      if (!!g) {
        alert("err")
      }else{
        this.dis = true
        alert("saved")
      }
    }else{
      if (this.password.length <= 3) {
        alert("Password harus lebih dari 3 huruf/angka")
      }else{
        var profile = {
        name: this.name,
        username: uname[0].username,
        email: this.email,
        password: this.password,
        description: this.des,
        phone: this.phone,
        address: this.address,
        zipcode: this.zipcode
          }
        var g = localStorage.setItem("profile", JSON.stringify(profile))
        if (!!g) {
          alert("err")
        }else{
          this.dis = true
          alert("saved")
        }
      }
    }
   },
   upd(){
    var uname = JSON.parse(sessionStorage.getItem("admin"))
    if ((null && undefined) == this.password ) {
      var profile = {
        name: this.name,
        username: uname[0].username,
        email: this.email,
        password: uname[0].password,
        description: this.des,
        phone: this.phone,
        address: this.address,
        zipcode: this.zipcode
      }
    var g = sessionStorage.setItem("admin", JSON.stringify(profile))
    if (!!g) {
      alert("err")
    }else{
      this.dis = true
      alert("saved")
    }
    }else{
      if (this.password.length <= 3) {
        alert("Password harus lebih dari 3 huruf/angka")
      }else{
        var profile = {
        name: this.name,
        username: uname[0].username,
        email: this.email,
        password: this.password,
        description: this.des,
        phone: this.phone,
        address: this.address,
        zipcode: this.zipcode
      }
    var g = sessionStorage.setItem("admin", JSON.stringify(profile))
    if (!!g) {
      alert("err")
    }else{
      this.dis = true
      alert("saved")
    }
      }
    }
   },
  },
  created(){
    var a = JSON.parse(localStorage.getItem("profile"))
    if (!a) {
      // ...
    } else {
      this.username = a.username
      this.name = a.name
      this.email = a.email
      this.des = a.description
      this.phone = a.phone
      this.address = a.address
      this.zipcode = a.zipcode
    }
  }
}
</script>